import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import registerServiceWorker from './registerServiceWorker';

const el = document.getElementById('tuition-calc');

ReactDOM.render(<App
  totalCreditHours={el.getAttribute('data-totalCreditHours')}
  universityTuition={el.getAttribute('data-unituition')}
  collegeTuition={el.getAttribute('data-colltuition')}
  universityFee={el.getAttribute('data-unifee')}
/>, el);



