import React, { Component } from 'react';
import './App.css';
import styled from 'styled-components';
import InputRange from 'react-input-range';
import NumberFormat from 'react-number-format';
import '../node_modules/react-input-range/lib/css/index.css';

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      value: null,
      costPerCredit: null,
      totalCreditHours: null,
      cost: 0
    };

    this.updateCredits = this.updateCredits.bind(this);
    this.updateCost = this.updateCost.bind(this);
  }

  calculateCost() {
    return {cost: this.state.value * this.state.costPerCredit}
  }

  componentWillMount(props) {
    this.getCredits(1 , this.dataCredit * 2/3);
    this.setState({
      totalCreditHours: this.dataCredit,
      costPerCredit: this.costPerCredit,
    });
  }

  componentDidMount(props) {
    const cost = this.state.costPerCredit * this.state.value;
    this.setState({ cost });
  }

  getCredits(min, max) {
    const credits = Math.floor(Math.random() * (Math.floor(max)) - Math.ceil(min));
    this.setState({value: credits});
  }



  universityFee = this.props.universityFee;
  universityTuition = this.props.universityTuition;
  collegeTuition = this.props.collegeTuition;

  costPerCredit = parseFloat(this.universityFee);
  costPerCredit =+ parseFloat(this.universityTuition);
  costPerCredit =+ parseFloat(this.collegeTuition);
  dataCredit = parseFloat(this.props.totalCreditHours);

  updateCredits(e) {
    e.preventDefault();
    this.setState({
      value: e.target.value });
  }

  updateCost() {
    this.setState(this.calculateCost);
  }

  render() {

    const updateSlider = value => {
      this.setState({value: value})
    };

    return (
      <div className="tuition-slider">
        <h2 className="tuition-slider__title">What Does It Cost?</h2>
        <div className="tuition-slider__subhead">
          Use slider to generate numerical cost
        </div>
        <RangeSliderContainer>
          <InputRange value={this.state.value} onChange={value => updateSlider( value )} onChangeComplete={this.updateCost} maxValue={this.state.totalCreditHours}/>
        </RangeSliderContainer>
        <TuitionForm>
          <TuitionFieldLabel>
          <TuitionField customInput={TuitionField} displayType={'text'} small type="number" name="credits" value={this.state.value} onChange={this.updateCredits}/>
            Credits
          </TuitionFieldLabel>
          <TuitionFieldLabel>
            <TuitionField customInput={TuitionField} displayType={'text'} thousandSeparator={true} prefix={'$'} medium type="number" name="costPerCreditHour" value={this.state.costPerCredit} onChange={value=> this.setState({ value })}/>
            Cost Per Credit Hour
          </TuitionFieldLabel>
          <TuitionFieldLabel>
            <TuitionField customInput={TuitionField} displayType={'text'} thousandSeparator={true} prefix={'$'} blue type="number" name="cost" value={this.state.cost} onChange={this.updateCost}/>
            Cost
          </TuitionFieldLabel>
        </TuitionForm>
        <div className="tuition-slider__footer">
          <p>This program requires <strong>{this.dataCredit} credit hours</strong> for degree completion.</p>
          <p><strong>This is only an estimate.</strong></p>
        </div>
      </div>
    );
  }
}

const TuitionForm = styled.form`
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  margin: 0 auto;
`;

const TuitionFieldLabel = styled.label`
  display: flex;
  flex-flow: column;
  text-align: center;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.5em;
`;

const TuitionField = styled(NumberFormat)`
  font-size: 3em;
  font-weight: bold;
  padding: 0.4em;
  box-shadow: inset -1px 1px 6px 0px ${props => props.blue ? '#fff' : '#000'};
  border: none;
  border-radius: 5px;
  text-align: center;
  background: ${props => props.blue ? '#002855'
    : props.gold ? '#eaaa00'
    : "#fff"}
  width: ${props => props.small ? '1.5em'
    : props.medium ? '2.5em'
    : '3.5em'}
  color: ${props => props.blue ? '#ffffff'
    : '#002855'}
  -moz-appearance:textfield;
  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
`;

const RangeSliderContainer = styled.div`
  margin-bottom: 2rem;

  .input-range {
    height: 4rem;
  }
  .input-range__label--min,
  .input-range__label--max {
    display: none;
  }
  .input-range__label--value {
    top: -4rem;
  }
  .input-range__track {
    height: 2rem;
  }
  .input-range__track--active {
    background: #eaaa00;
  }
  .input-range__slider {
    border-radius: 0;
    height: 3rem;
    margin-top: -2.5rem;
    background: #002855;
    border-color: #002855;
  }
`;

export default App;



